'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { doc, getDoc } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import Image from 'next/image';
import { formatCurrency } from '@/src/utils/formatCurrency';

export default function VehicleModelDetailPage() {
  const params = useParams();
  const router = useRouter();
  const modelId = Array.isArray(params?.id) ? params.id[0] : params?.id; // ‚úÖ x·ª≠ l√Ω ch√≠nh x√°c type

  const [model, setModel] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchModel = async () => {
      if (!modelId) return;

      try {
        const docRef = doc(db, 'ebikeModels', modelId);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          setModel(docSnap.data());
        } else {
          console.error('Model not found');
        }
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchModel();
  }, [modelId]);

  if (loading) {
    return <p className="text-center py-10 text-gray-500">Loading...</p>;
  }

  if (!model) {
    return <p className="text-center py-10 text-red-500">Vehicle model not found.</p>;
  }

  return (
    <section className="bg-gray-100 py-10 px-4">
      <div className="max-w-5xl mx-auto bg-white shadow rounded-xl overflow-hidden">
        <div className="grid md:grid-cols-2 gap-6">
          <Image
            src={model.imageUrl}
            alt={model.name}
            width={800}
            height={600}
            className="object-cover w-full h-[400px]"
          />

          <div className="p-6 space-y-4">
            <h1 className="text-2xl font-bold text-[#00d289]">{model.name}</h1>
            <p className="text-gray-700">{model.description}</p>

            <table className="text-sm w-full mt-4">
              <tbody>
                <tr><td className="font-medium pr-3">üîã Battery:</td><td>{model.batteryCapacity} Wh</td></tr>
                <tr><td className="font-medium pr-3">‚ö° Top Speed:</td><td>{model.topSpeed} km/h</td></tr>
                <tr><td className="font-medium pr-3">üìè Range:</td><td>{model.range} km</td></tr>
                <tr><td className="font-medium pr-3">üèãÔ∏è Max Load:</td><td>{model.maxLoad} kg</td></tr>
                <tr><td className="font-medium pr-3">‚öñÔ∏è Weight:</td><td>{model.weight} kg</td></tr>
              </tbody>
            </table>

            <div className="text-gray-800 mt-4 space-y-1">
              <p>üí∞ <strong>Daily:</strong> {formatCurrency(model.pricePerDay)}</p>
              {model.pricePerWeek && <p>üìÖ <strong>Weekly:</strong> {formatCurrency(model.pricePerWeek)}</p>}
              {model.pricePerMonth && <p>üóìÔ∏è <strong>Monthly:</strong> {formatCurrency(model.pricePerMonth)}</p>}
            </div>

            <div className="flex gap-4 mt-6">
              <button
                onClick={() => router.back()}
                className="px-4 py-2 border border-gray-300 rounded hover:bg-gray-100"
              >
                ‚Üê Back
              </button>
              <button className="px-6 py-2 bg-[#00d289] text-white font-bold rounded hover:bg-[#00b577] transition">
                Rent this vehicle
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
